import { Component } from '@angular/core';
import { NavController, LoadingController, ToastController } from '@ionic/angular';
import { PostServiceProvider } from './../post-service.service';


@Component({
    selector: 'app-tab3',
    templateUrl: 'tab3.page.html',
    styleUrls: ['tab3.page.scss']
})
export class Tab3Page {

    arrayPosts: any; //Creamos la variable donde guardaremos los datos que nos retorna el servicio

    constructor(
        public navCtrl: NavController,
        public postServices: PostServiceProvider,
        private toastCtrl: ToastController,
        private loadingCtrl: LoadingController
    ) { }


    ngOnInit() {
        // this.getPostsId('1');//Llamamos a la función getPost cuando la vista se cargue
        this.getAllPosts();//Llamamos a la función getPost cuando la vista se cargue
    }

    getAllPosts() { //llamamos a la funcion getPost de nuestro servicio.
        this.postServices.getAllTasks().subscribe(res => {
            console.log(res);
            this.arrayPosts = res;
        });
    }
    getPostsId(id: string) { //llamamos a la funcion getPost de nuestro servicio.
        this.postServices.getTask(id).subscribe(res => {
            console.log(res);
            this.arrayPosts = Array.of(res);
        });
    }
}


